import React, { useEffect, useState } from 'react';
import styles from './styles.module.css';
import { useDispatch, useSelector } from 'react-redux';
import { fetchCities } from './locations';
import { addLocation } from '../../../../../../../redux/order/shipping/actions';


const Form = ({ data, setData, changeHandler }) => {
    
    const dispatch = useDispatch();
    const { addressInfo } = useSelector(state => state.shipping);
    const [locationState, setLocationState] = useState('');
    
    let [cities, setCities] = useState([]);
    
    useEffect(() => {
        fetchCities(locationState, setCities);
    }, [locationState, setCities]);
    
    useEffect(() => {
        addressInfo && addressInfo.state && setData({ ...data, state: addressInfo.state })
        addressInfo && addressInfo.city && setData({ ...data, city: addressInfo.city })
        addressInfo && addressInfo.street && setData({ ...data, street: addressInfo.street })
        addressInfo && addressInfo.zipCode && setData({ ...data, zipCode: addressInfo.zipCode })
    }, [addressInfo]);
    
    const addLocationHandler = (e) => {
        e.preventDefault();
        dispatch(addLocation(data));
    };

    return (
        <form className = {styles.form}>

            <div className={styles.container}>
                <label htmlFor=''></label>
                <select  name="state" onChange={e => setLocationState(e.target.value)}>
                    <option value="0">    </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=" "> </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=" "> </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=" "> </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value="  ">  </option>
                    <option value=""></option>
                    <option value="  ">  </option>
                    <option value=""></option>
                    <option value="  ">  </option>
                    <option value=" "> </option>
                    <option value=" "> </option>
                    <option value=""></option>
                </select>
            </div>

            <div className={styles.container}>
                <label htmlFor=''></label>
                <select id = '' 
                name = 'city'
                onChange = {e => setData({
                    ...data,
                    city: e.target.value,
                })}
                >
                    {cities.map(city => <option key = {city} value = {city}>{city}</option>)}
                </select>
            </div>   
            
            <div className={styles.container}>
                <label></label>
                <input id = '' type='text'
                placeholder =' ...'
                name = 'street'
                value = {data.street}
                onChange = {changeHandler}
                />
            </div>

            <div className={styles.container}>
                <label> </label>
                <input id = '' type='text'
                placeholder ='235326241'
                name = 'zipCode'
                value = {data.zipCode}
                onChange = {changeHandler}
                />
            </div>

            <button onClick={addLocationHandler}>
                
            </button>

        </form>
    );
};

export default Form;